import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { environment } from '@environments/environment';

@Injectable({ providedIn: 'root' })
export class CountryService {
    constructor(private http: HttpClient) { }

    getContinents() {
        return this.http.get(`${environment.apiUrl}/continents`);
    }

    getAll(continent: string) {
        let queryString = `${environment.apiUrl}/countries`;
        if (continent) {
            queryString = `${queryString}?continent_id=${continent}`
        }
        return this.http.get(queryString);
    }

    getLocations(country: string, continent: string) {
        let queryString = `${environment.apiUrl}/locations`;
        return this.http.get(`${queryString}?country_id=${country}`);
    }

}