import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '@environments/environment';

@Injectable({ providedIn: 'root' })
export class TrackdayService {
    constructor(private http: HttpClient) { }

    getAll(data) {
        let _queryString = `${environment.apiUrl}/trackdays`;
        if (data) {
            _queryString = data.filter ? `${_queryString}?${this.jsonToQueryString(data)}` : _queryString;
        }
        return this.http.get(_queryString);
    }

    get(id) {
        return this.http.get(`${environment.apiUrl}/trackdays/${id}`);
    }

    getFilterTrackdays() {
        return this.http.get(`${environment.apiUrl}/filterTrackday`);
    }

    jsonToQueryString(json) {
        if (json == null) {
            return '';
        } else {
            const queryString = Object.keys(json).map(key => {
                if (json[key] instanceof Array) {
                    let string = '';
                    json[key].forEach((v, i) => {
                        string += `${key}[${i}]=${v}&`;
                    });

                    return string;
                } else if (json[key] instanceof Object) {
                    return this.jsonToQueryString(json[key]);
                } else {
                    return key + '=' + json[key];
                }
            }).join('&');

            return queryString;
        }
    }
}