import { Component, EventEmitter, Input, Output, OnInit, ViewChild, AfterViewInit, ChangeDetectorRef } from '@angular/core';
import { CountryService, OrganizerService, GradeService, RangeService, VehicleService, TrackdayService } from '@app/_services';
import { IonRangeSliderComponent } from 'ng2-ion-range-slider';
import { first } from 'rxjs/operators';
import { Options } from '@angular-slider/ngx-slider';
import { FormGroup, FormControl } from '@angular/forms';

@Component({
    selector: 'trackday-list-filter',
    templateUrl: 'trackday-list-filter.component.html',
    styleUrls: ['./trackday-list-filter.component.css']
})
export class TrackdayListFilterComponent implements OnInit, AfterViewInit {
    locations: [];
    countries: [];
    continents: [];
    organizers: [];
    grades: [];
    vehicleTypes: Array<Object> = [];
    dropdownSettings: Object;
    selectedVehicles = [];
    @Input() labels: object;

    selected = null;

    @Input('continent') continent = null;
    @Output() continentChange = new EventEmitter<string>();

    @Input('country') country = null;
    @Output() countryChange = new EventEmitter<string>();

    @Input('organizer') organizer = null;
    @Output() organizerChange = new EventEmitter<string>();

    @Input('vehicle') vehicle = null;
    @Output() vehicleChange = new EventEmitter<string>();

    @Input('grade') grade = null;
    @Output() gradeChange = new EventEmitter<string>();

    @Input('location') location = null;
    @Output() locationChange = new EventEmitter<string>();

    @Input('priceRange') priceRange;
    @Input('circuitLengthRange') circuitLengthRange;

    @Output() filter = new EventEmitter();

    @ViewChild('lengthSlider') lengthSlider: IonRangeSliderComponent;
    @ViewChild('priceSlider') priceSlider: IonRangeSliderComponent;

    sliderForm: FormGroup = new FormGroup({
        sliderControl: new FormControl([0, 7004])
    });
    minValue: number = 0;
    maxValue: number = 7004;
    options: Options = {
        floor: 0,
        ceil: 7004,
        getPointerColor: (value: number): string => {
            return '#e3001b';
        },
        getSelectionBarColor: (value: number): string => {
            return '#e3001b';
          }
    };

    priceForm: FormGroup = new FormGroup({
        priceControl: new FormControl([0, 6000])
    });
    minPriceValue: number = 0;
    maxPriceValue: number = 6000;
    priceOptions: Options = {
        floor: 0,
        ceil: 6000,
        getPointerColor: (value: number): string => {
            return '#e3001b';
        },
        getSelectionBarColor: (value: number): string => {
            return '#e3001b';
          }
    };


    showForm = true;
    useFilter = false;
    minPrice: Number = 0;
    maxPrice: Number = 250000;
    priceFrom: Number = 0;
    priceTo: Number = 250000;
    minLength: Number = 0;
    maxLength: Number = 20000;
    lengthFrom: Number = 0;
    lengthTo: Number = 0;
    priceStep: Number = 100;

    constructor(
        private countryService: CountryService,
        private organizerService: OrganizerService,
        private gradeService: GradeService,
        private vehicleService: VehicleService,
        private rangeService: RangeService,
        private trackdayService: TrackdayService,
        private cdRef:ChangeDetectorRef
    ) { }

    ngAfterViewInit() {
        this.selected = null;
        this.cdRef.detectChanges();
    }

    ngOnInit() {
      this.dropdownSettings = {
        singleSelection: false,
        idField: 'item_id',
        textField: 'item_text',
        selectAllText: this.labels['Select_All'],
        unSelectAllText: 'UnSelect All',
        itemsShowLimit: 2,
        allowSearchFilter: true
      };

      // // get continents
      // this.countryService.getContinents().pipe(first()).subscribe((response: any) => {
      //     this.continents = response;
      // });
      // // get organizers
      // this.organizerService.getAll().pipe(first()).subscribe((response: any) => {
      //     this.organizers = response;
      // });
      // // get grades
      // this.gradeService.getAll().pipe(first()).subscribe((response: any) => {
      //     this.grades = response;
      // });
      // // get vehicle types
      // this.vehicleService.getAll().pipe(first()).subscribe((response: any) => {
      //     this.vehicleTypes = response;
      //
      //     this.vehicleTypes = response.map(vehicleType => ({
      //         item_id: vehicleType.id,
      //         item_text: vehicleType.name,
      //     }));
      // });
      //
      // this.rangeService.getPriceRange().pipe(first()).subscribe((response: any) => {
      //     this.minPrice = response.min;
      //     this.maxPrice = response.max;
      //     this.priceFrom = response.min;
      //     this.priceTo = response.max;
      // });
      //
      // this.rangeService.getLengthRange().pipe(first()).subscribe((response: any) => {
      //     this.minLength = response.min;
      //     this.maxLength = response.max;
      //     this.lengthFrom = response.min;
      //     this.lengthTo = response.max;
      // });

      this.trackdayService.getFilterTrackdays().pipe(first()).subscribe((response: any) => {
          this.continents = response.continents;
          this.continents = this.getAttributeData(response.continents);

          this.organizers = response.organizers;
          this.grades = response.grades;
          this.vehicleTypes = response.vehicleTypes;

          this.vehicleTypes = response.vehicleTypes.map(vehicleType => {
            const vehicleDataArray = {
                item_id: vehicleType.id
            };
            if(vehicleType.attributes.length > 0) {
                vehicleType.attributes.forEach(element => {
                    const langName = JSON.parse(element.attribute_data);
                    if(localStorage.lang == element.locale && langName.name !== '') {
                        vehicleDataArray['item_text'] = langName.name;
                    } else {
                        vehicleDataArray['item_text'] = vehicleType.name;
                    }
                });
            } else {
                vehicleDataArray['item_text'] = vehicleType.name;
            }
                   
            return vehicleDataArray;
            });            

          this.minPrice = response.priceRange.min;
          this.maxPrice = response.priceRange.max;
          this.priceFrom = response.priceRange.min;
          this.priceTo = response.priceRange.max;

          this.minLength = response.lengthRange.min;
          this.maxLength = response.lengthRange.max;
          this.lengthFrom = response.lengthRange.min;
          this.lengthTo = response.lengthRange.max;
      });

      this.handleClearFilter();
    }

    public handleChangeContinent() {
        this.continentChange.emit(this.continent);
        this.handleFilter(true);
        if(this.continent != '') {
            // get countries
            this.countryService.getAll(this.continent).pipe(first()).subscribe((response: any) => {
                // this.countries = response;
                this.countries = this.getAttributeData(response);
            });
        } else {
            this.country = '';
            this.location = '';
            this.countries = [];
            this.locations = [];
        }
    }

    public getAttributeData(data) {
        return data.map(res => {
            const dataArray = {
                id: res.id
            };
            if(res.attributes.length > 0) {
                res.attributes.forEach(element => {
                    const langName = JSON.parse(element.attribute_data);
                    if(localStorage.lang == element.locale && langName.name !== '') {
                        dataArray['name'] = langName.name;
                    } else {
                        dataArray['name'] = res.name;
                    }
                });
            } else {
                dataArray['name'] = res.name;
            }
               
            return dataArray;
        });
    }

    public handleChangeCountry() {
        this.countryChange.emit(this.country);
        this.handleFilter(true);
        if(this.country != '') {
            // get location
            this.countryService.getLocations(this.country, this.continent).pipe(first()).subscribe((response: any) => {
                this.locations = response;
            });
        } else {
            this.location = '';
            this.locations = [];
        }
    }

    public handleLocationChange() {
        this.locationChange.emit(this.location);
        this.handleFilter(true);
    }

    public handleChangeOrganizer() {
        this.organizerChange.emit(this.organizer);
        this.handleFilter(true);
    }

    public handleChangeVehicle() {
        // this.vehicleChange.emit(this.vehicle);
        this.handleFilter(true);
    }

    public handleChangeGrade() {
        this.gradeChange.emit(this.grade);
        this.handleFilter(true);
    }

    public priceSliderChange(e) {
        this.priceRange = e;
        this.handleFilter(true);
    }

    public circuitSliderChange(e) {
        this.circuitLengthRange = e;
        this.handleFilter(true);
    }

    public handleChoosedDate(e) {
        this.selected = e;
        this.handleFilter(true);
    }

    public handleToggleFilter() {
        this.showForm = !this.showForm;
    }

    public handleClearFilter() {
        this.selected = null;
        this.continent = null;
        this.country = null;
        this.organizer = null;
        this.selectedVehicles = [];
        this.grade = null;
        this.location = null;
        this.useFilter = false;
        this.priceRange = null;
        this.circuitLengthRange = null;
        this.priceFrom = this.minPrice;
        this.priceTo = this.maxPrice;
        this.lengthFrom = this.minLength;
        this.lengthTo = this.maxLength;

        if (this.sliderForm) {
        //   this.lengthSlider.update({ from: this.minLength, to: this.maxLength });
            this.sliderForm.reset({sliderControl: [this.options.floor, this.options.ceil]});
        }

        if (this.priceSlider) {
        //   this.priceSlider.update({ from: this.minPrice, to: this.maxPrice });
          this.priceForm.reset({priceControl: [this.priceOptions.floor, this.priceOptions.ceil]});
        }

        this.handleFilter(false);
    }

    private handleFilter(filter) {
        const data = {
            filter: filter ? filter : this.useFilter,
            continent: this.continent,
            country: this.country ? [this.country] : null,
            vehicleType: this.selectedVehicles ? this.selectedVehicles.map(vehicle => vehicle.item_id).join(';') : null,
            grade: this.grade,
            price: this.priceRange ? this.priceRange.value + ';' + this.priceRange.highValue : null,
            length: this.circuitLengthRange ? this.circuitLengthRange.value + ';' + this.circuitLengthRange.highValue : null,
            startDate: this.selected && this.selected.startDate ? this.selected.startDate.format('YYYY-MM-DD') : null,
            endDate: this.selected && this.selected.endDate ? this.selected.endDate.format('YYYY-MM-DD') : null,
            location: null,
            organizer: null,
        };

        if (this.location) {
            data.location = this.location;
        }

        if (this.organizer) {
            data.organizer = this.organizer;
        }

        this.filter.emit(data);
    }
}
