import { Component } from '@angular/core';
import { first } from 'rxjs/operators';

import { TrackdayService, TranslateService } from '@app/_services';
declare var L: any;

import Config from '../../../config';

@Component({
    templateUrl: 'trackday.component.html',
    styleUrls: ['./trackday.component.css'],
})
export class TrackdayComponent {
    loading = false;
    trackdays: [];
    totalTrackdays: [];
    baseUrl: string;
    labels = null;

    constructor(private trackdayService: TrackdayService, private translateService: TranslateService) { }

    ngOnInit() {
        this.loading = true;
        // this.baseUrl = Config.baseUrl;
        this.baseUrl = localStorage.baseUrl;

        this.translateService.translate().subscribe((response: any) => {
            this.labels = response;
        });  
        
    }

    public trackdayFilter(data) {
        this.loadTrackdays(data);
    }

    private loadTrackdays(data) {
        data['lang'] = localStorage.lang;
        this.loading = true;
        this.trackdayService.getAll(data).pipe(first()).subscribe((response: any) => {
            this.loading = false;
            const trackdays = response.trackdays.data.map(trackday => {
                const data = {
                    ...trackday,
                    book_url: localStorage.trackdayApiSnipletId ? this.handleBookUrl(trackday.book_url + '?sid=' + localStorage.trackdayApiSnipletId) : this.handleBookUrl(trackday.book_url),
                    organizer_detail_url: this.handleBookUrl(trackday.organizer_detail_url)
                    // vehicles: Object.keys(trackday.vehicles).map(key => trackday.vehicles[key]),
                }
                Object.keys(trackday.vehicles).map(track => {
                    data['vehicleType'] = this.getAttributeData(response.vehicleTypes.find(vehicleType => vehicleType.id === trackday.trackday_vehicle_type_id));
                });
                return data;
            });
            this.trackdays = trackdays;
            if (!data.filter) {
                this.totalTrackdays = trackdays;
            }
        });
    }

    public getAttributeData(data) {
        const dataArray = {
            id: data.id
        };
        if(data.attributes.length > 0) {
            const attribute = data.attributes.find(element => element.locale === localStorage.lang);

            console.log(attribute);
            if (attribute) {
                const attributeData = JSON.parse(attribute.attribute_data);
                dataArray['name'] = attributeData.name || data.name;
            } else {
                dataArray['name'] = data.name;
            }
        } else {
            dataArray['name'] = data.name;
        }
        
        return dataArray;
    }

    public getTitle()
    {
        const title = this.labels['Found_X_out_of_Y_track_day_offers'];
        const titleX = title.replace('X', (this.trackdays || []).length, title);
        const titleY = titleX.replace('Y', (this.totalTrackdays || []).length, titleX);
        return titleY;
    }

    public handleBookUrl(url) {
        const lang = url.split("/")[3];
        const urlSplit = new URL(url).pathname.split('/');
        urlSplit[1] = localStorage.lang;
        let newUrlString = '';
        urlSplit.forEach(element => {
            newUrlString += element+'/';
        });
        let wihtoutSlash;
        if(newUrlString.substr(-1) === '/') {
            wihtoutSlash = newUrlString.substr(0, newUrlString.length - 1);
        }
        
        return new URL(url).origin+wihtoutSlash;
    }
}
